

















































import { Vue, Component } from "vue-property-decorator";
import ArticlePreview from "@/components/article/ArticlePreview.vue";
import { Article } from "../store/models";
import articles from "../store/modeules/articles";
import users from "../store/modeules/users";

@Component({
  components: {
    ArticlePreview
  }
})
export default class Home extends Vue {
  tab: string = "";
  tags?: string[] | null = [];

  created() {
    users.userAlredyLoggedIn();
    this.GetGlobalFeed();
    articles.fetchTags().then(() => (this.tags = articles.tags));
  }

  get Feed(){
    return articles.Feed || [];
  }

  GetMyFeed() {
    if (!users.usernameExists) return;
    this.tab = "user";
    articles.refreshGlobalFeed("user");
  }

  GetGlobalFeed() {
    this.tab = "global";
    articles.refreshGlobalFeed("global");
  }

  get username() {
    return users.usernameExists;
  }

  filterByTag(tag: string) {
    this.tab = tag;
    articles.getFeedByQuery("tag=" + tag);
  }
}
